import React, { useState, useEffect } from 'react';
import MathInput from 'react-math-keyboard';

const TestPage = ({ userData, testData, onSubmit, loading }) => {
  const [timeLeft, setTimeLeft] = useState(0);
  const [answers, setAnswers] = useState({
    1: '',
    2: '',
    3: ''
  });
  const [startTime, setStartTime] = useState('');

  useEffect(() => {
    if (testData.minutes) {
      const totalSeconds = testData.minutes * 60;
      setTimeLeft(totalSeconds);
      setStartTime(new Date().toISOString());
    }
  }, [testData]);

  useEffect(() => {
    if (timeLeft > 0) {
      const timer = setTimeout(() => {
        setTimeLeft(timeLeft - 1);
      }, 1000);
      return () => clearTimeout(timer);
    }
  }, [timeLeft]);

  const formatTime = (seconds) => {
    const hours = Math.floor(seconds / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    const secs = seconds % 60;
    return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  const handleMathInputChange = (questionId) => (latexString) => {
    setAnswers(prev => ({
      ...prev,
      [questionId]: latexString
    }));
  };

  const handleTextAnswerChange = (value) => {
    setAnswers(prev => ({
      ...prev,
      3: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const endTime = new Date().toISOString();
    onSubmit(answers, startTime, endTime);
  };

  return (
    <div className="container">
      <div className="header">
        <h1>Test</h1>
        <h2>{userData.firstName} {userData.lastName}</h2>
      </div>

      <div className="timer">
        Qolgan vaqt: {formatTime(timeLeft)}
      </div>

      <form onSubmit={handleSubmit}>
        <div className="question-card">
          <div className="question-number">
            1-savol: Matematik ifodani yeching
          </div>
          <div className="math-input-container">
            <MathInput 
              setValue={handleMathInputChange(1)}
              initialLatex={answers[1]}
            />
          </div>
        </div>

        <div className="question-card">
          <div className="question-number">
            2-savol: Funksiya hosilasini toping
          </div>
          <div className="math-input-container">
            <MathInput 
              setValue={handleMathInputChange(2)}
              initialLatex={answers[2]}
            />
          </div>
        </div>

        <div className="question-card">
          <div className="question-number">
            3-savol: Yechim usulingizni tushuntiring
          </div>
          <textarea
            className="text-answer"
            value={answers[3]}
            onChange={(e) => handleTextAnswerChange(e.target.value)}
            placeholder="Yechim usulingizni bu yerga yozing..."
            disabled={loading}
          />
        </div>

        <button type="submit" className="btn" disabled={loading}>
          {loading ? 'Yuborilmoqda...' : 'Testni yakunlash'}
        </button>
      </form>
    </div>
  );
};

export default TestPage;
